import {
  registerDecorator, ValidationOptions
} from 'class-validator';
import { UniqueArrayObjectsRule } from './unique-array-objects.rule';

export function IsUniqeArrayObjets<T>(
  property?: keyof T,
  validationOptions?: ValidationOptions,
) {
  return (object: any, propertyName: string) => {
    registerDecorator({
      name: 'IsUniqeArrayObjets',
      target: object.constructor,
      propertyName,
      constraints: [property],
      options: validationOptions,
      validator: UniqueArrayObjectsRule
    });
  };
}